.card-plano {
    width: calc(100% / 3);
    border: 1px solid var(--color-primary);
    border-radius: 5px;
    margin: 15px;
}

.card-plano-header {
    background-color: var(--color-primary);
    text-align: center;
    padding: 5px;
}

.card-plano-title {
    font-size: 12pt;
    color: #ffffff;
    font-weight: 900;
}

.card-plano-content {
    margin: 0 auto;
}

.card-plano-logo {
    margin: 5px auto;
    width: 100%;
    height: 2%;
}

.card-plano-logo > img {
    width: 90px;
    margin: 10px auto;
    display: block;
}

.card-plano-datas {
    width: 100%;
}

.card-plano-datas > table {
    width: 100%;
    margin: 15px 0px 30px;
    box-shadow: none;
}

.card-plano-datas > table > tr > td {
    text-align: center;
    padding: 2px;
    font-size: 10pt;
}

.card-plano-footer {
    padding: 15px 0px;
    border-top: 1px solid var(--color-primary);
    width: 100%;
}

.card-plano-footer > button {
    margin: 0 auto;
    display: block;
    width: 70%;
    background-color: var(--color-primary);
    color: #ffffff;
}

.wrapper-total {
    width: 100%;
}

.wrapper-total > h1 {
    text-align: center;
}

.total-valor {
    color: var(--color-primary);
}

.ansbox {
    text-align: center;
    background-color: black;
    color: white;
    outline: 2px solid black;
    border: 1px solid white;
    width: 12em;
    margin: 10px auto;
    display: block;
}


@media (min-width: 320px){
    .card-plano-footer{
        display: flex;
        flex-direction: column;       
    }

    .card-plano-footer button{
        margin-bottom: 15px;
    }

    .card-plano-footer a{
        color: white;
        text-decoration: none;
    }
}

@media (min-width: 1024px){
    .card-plano-footer{
        display: flex;
        flex-direction: row;       
    }

    .card-plano-footer button{
     margin: 15px;
    }
}