.body {
    display: flex;
    flex-direction: column ;
    justify-content: center;
    align-items: center;
    height: 250px;
    border-bottom: 1px solid black;
    background-color: #bee2cd;
}

.cardPageCli {
    border: 2px solid #1F3261;
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.40); 
    width: 350px;
    text-align: center;
    border-radius: 5px;
    margin: 10px;
    cursor: pointer;
    transition: 0.5s;
}

.cardPageCli:hover {
    box-shadow: 0px 3px 5px 5px rgba(31, 50, 97, 0.5);     
}

.span-body {
    font-size: 20px;
    margin-bottom: 18px;
    font-weight: 800;
    color: #1F3261;
}

.card-footer {
    padding: 10px;
    color: white;
    background-color: #1F3261;
}

.card-footer > span {
    font-weight: 500;
    font-size: 15px;
}

.desc {
    color: white;
    font-size: 20px;
    text-shadow: 2px 2px 1px black;
    font-weight: 500;
}