      margin: 0; 
}

input {
    color: #000;
}

.form{
    box-shadow: 6px 3px 13px rgba(0, 0, 0, 0.5);
    border-radius: 10px;
    display: flex;
    justify-content: center;
    width: 80%;
    max-width: 715px;
    height: 330px;
