    padding-top: 15px;
    -webkit-appearance: none;
       -moz-appearance: none;
            appearance: none;
}

#select1 {
    margin-bottom: 15px;
}

#select-estado {
    display: none;
}

#myModal{
    display: none; 
    position: fixed; 
    z-index: 1; 
    padding-top: 100px; 
    left: 0;
    top: 0;
    width: 100%; 
    height: 100%; 
    overflow: auto; 
    background-color: rgb(0,0,0); 
    background-color: rgba(0,0,0,0.4); 
}

#modal-content {
    background-color: #fefefe;
    margin: auto;
    padding: 20px;
