.coluna-card{
    display: flex;
    flex-direction: column;
    background-color: #f8f8f8;
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.40); 
    margin: 25px;
    text-align: center;
    transition: all 0.5s;
    width: calc(100% / 3);
    padding: 5px;
}

.coluna-card:hover {
    cursor: pointer;
    box-shadow: 0px 1px 17px rgba(88, 72, 235, 0.781);
}

.card{
    width: calc(90% / 3);
    min-height: 150px;
    height: auto;
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
    margin: 10px 5px;
    background-color: #f8f8f8;
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.40);
}

